
#particles-js {
  width: 100%;
  height: calc(100% - 100px);
  position: absolute;
}
#logo{
  width: 110px;
  height: auto;
}
.header-area.single-page{
  background-color: #000000;
  position: fixed;
  z-index: 999;
  top: 0;
  width: 100%;
}
.header-top{
  padding-bottom: 0px;
}
.top-one{
  padding-top: 100px;
}
.main-menu ul li a img{
  width: 45px;
}
.main-menu ul li a{
  padding: 8px;
}
.login-btn {
  width: 150px;
  height: 45px;
  border: 1px solid #ffffff;
  border-radius: 10px;
  color: #ffffff;
  font-size: large;
  background-color: #000000;
}
.login-btn:hover{
  border: 1px solid #e99d42;
  background-color: #e99d42;
  color: #000000;
}
.launch-btn{
  width: 160px;
  height: 50px;
  text-align: center;
  vertical-align: middle;
  border: 1px solid #ffffff;
  border-radius: 10px;
  color: #ffffff;
  background-color: #000000;
}
.launch-btn:hover{
  border: 1px solid #e99d42;
  background-color: #e99d42;
}
.connDiv{
  width: 100%;
  text-align: center;
  border-radius: 20px;
  padding: 10px;
  cursor: pointer;
}
.connDiv:hover{
  background-color: #CCCCCC;
}
.connDiv-name{
  padding-top: 10px;
  font-size: 30px;
  color: #ff9f55;
}
.connDiv-desc{
  padding-top: 10px;
  font-size: 20px;
  color: black;
}
@media (min-width: 768px) and (max-width: 991.98px) {
.login-btn {
    width: 180px;
    height: 50px;
}
#logo{
    width: 120px;
}
.main-menu ul li a img{
    width: 45px;
}
}
@media (min-width: 576px) and (max-width: 767.98px) {
.login-btn {
    width: 160px;
    height: 40px;
}
#logo{
    width: 110px;
}
.main-menu ul li a img{
    width: 40px;
}
}
@media (max-width: 575.98px) {
.login-btn {
    width: 140px;
    height: 30px;
}
#logo{
    width: 100px;
}
.main-menu ul li a img{
    width: 30px;
}
}
.ahr{
  border: none;
}
ahr:link{
	color: #fff;
}
ahr:visited{
	color: #fff;
}
ahr:hover{
	color: #fff;
}
ahr:active{
	color: #fff;
}





